import React, { useState, useEffect } from 'react';

import Blog from './components/Blog';
import Footer from './components/Footer';
import Notification from './components/Notification';
import BlogForm from './components/BlogForm';
import Togglable from './components/Togglable';
import LoginForm from './components/LoginForm';

import blogService from './services/blogs';
import loginService from './services/login';

const App = () => {
  const [blogs, setBlogs] = useState([]);
  const [showAll, setShowAll] = useState(true);
  const [notificationMessage, setNotificationMessage] = useState('');
  const [notificationType, setNotificationType] = useState('');
  const [user, setUser] = useState(null);

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedBlogAppUser');

    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON);

      setUser(user);
      blogService.setToken(user.token);
    }
  }, []);

  useEffect(async () => {
    try {
      const blogs = await blogService.getAll();
      setBlogs(blogs);
    } catch (e) {
      setNotificationMessage(`Blog viewing failed with exception: ${e}`);
      setNotificationType('error');

      setTimeout(() => {
        setNotificationMessage('');
        setNotificationType('');
      }, 5000);
    }
  }, []);

  const addBlog = async (blogObject) => {
    try {
      const returnedBlog = await blogService.create(blogObject);

      setBlogs(blogs.concat(returnedBlog));

      setNotificationMessage(`Blog added with success`);
      setNotificationType('success');

      setTimeout(() => {
        setNotificationMessage('');
        setNotificationType('');
      }, 5000);
    } catch (exception) {
      setNotificationMessage(
        `Blog addition failed with exception: ${exception}`
      );
      setNotificationType('error');

      setTimeout(() => {
        setNotificationMessage('');
        setNotificationType('');
      }, 5000);
    }
  };

  const handleLogin = async (userObject) => {

    const username = userObject.username;
    const password = userObject.password;

    try {
      const user = await loginService.login({
        username,
        password,
      });

      window.localStorage.setItem('loggedBlogAppUser', JSON.stringify(user));

      blogService.setToken(user.token);
      setUser(user);

      setNotificationMessage(`Welcome in you dear ${user.name}`);
      setNotificationType('success');

      setTimeout(() => {
        setNotificationMessage('');
        setNotificationType('');
      }, 5000);
    } catch (exception) {
      setNotificationMessage(`wrong user name or password`);
      setNotificationType('error');
      setTimeout(() => {
        setNotificationMessage('');
        setNotificationType('');
      }, 5000);
    }
  };

  
  const loginForm = () => {
    return (
      <Togglable buttonLabel="login">
        <LoginForm handleSubmit={handleLogin} />
      </Togglable>
    );
  };

  const blogForm = () => (
    <Togglable buttonLabel="new blog">
      <BlogForm onSubmit={addBlog} />
    </Togglable>
  );

  const logout = () => {
    window.localStorage.removeItem('loggedBlogAppUser');

    setNotificationMessage(`Bye, ${user.name}`);
    setNotificationType('success');

    setTimeout(() => {
      setNotificationMessage('');
      setNotificationType('');
    }, 5000);

    blogService.setToken(null);
    setUser(null);
    loginForm();
  };

  return (
    <div>
      <h1>Blogs</h1>
      <Notification message={notificationMessage} type={notificationType} />

      {user === null ? 
        loginForm()
       : 
        <div>
          <p>
            {user.name} logged in
            <button onClick={() => logout()}> logout </button>
          </p>
          {blogForm()}
        </div>
      }

      <ul>
        {blogs.map((blog, i) => (
          <Blog key={i} blog={blog} />
        ))}
      </ul>

      <Footer />
    </div>
  );
};

export default App;
